/* ==========================================================================
   #LIST MENU
   ========================================================================== */
.header-menu {
  display: flex;

  @include mq(to768) {
    display: none;
  }

  @include mq(from768to1200) {
    background-color: rgba(194, 215, 232, 0.4);
    justify-content: center;
  }

  &__item {
    padding: 15px 16px;
    position: relative;
    cursor: pointer;

    @include mq(from768to1200) {
      padding: 15px 10px;
    }

    &::after {
      content: '';
      height: 2px;
      background-color: $primary-red;
      position: absolute;
      bottom: 0;
      left: 50%;
      right: 50%;
      opacity: 0;
      transition: all 0.5s ease-in;
    }

    a {
      color: $link-color;
      font-weight: 400;
      text-transform: uppercase;
      text-decoration: none;
      letter-spacing: 2.1px;
      transition: all 0.3s ease-in;
    }

    &:hover {
      &::after {
        opacity: 1;
        left: 0;
        right: 0;
      }

      a {
        color: $primary-red;
      }
    }
  }
}